.has_fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Nunito", sans-serif;
  font-size: 1.125rem;
  font-weight: 300;
  color: hsl(211, 52%, 34%);
  line-height: 1.3;
}
body.noscroll {
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:hover {
  text-decoration: none;
}

hr {
  border-top: 2px solid hsl(211, 52%, 34%);
  width: clamp(100px, 50%, 380px);
}

.invert {
  border-top: 1px solid white;
  width: clamp(100px, 50%, 380px);
}

.container {
  padding-top: 2.25rem;
  padding-right: 1.5625rem;
  padding-bottom: 2.25rem;
  padding-left: 1.5625rem;
  margin: 0;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-jc-se {
  justify-content: space-evenly;
}
.flex-jc-e {
  justify-content: end;
}
.flex-ai-c {
  align-items: center;
}
.flex-w {
  flex-wrap: wrap;
}

.grid {
  display: grid;
}

button,
.button {
  display: inline-block;
  padding: 0.875rem 2.1875rem;
  background: hsl(49, 6%, 34%);
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  color: hsl(105, 55%, 96%);
  font-weight: 400;
  text-align: center;
}
button:hover,
.button:hover {
  background: hsl(135, 8%, 72%);
  color: black;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

.header.open .overlay {
  display: block;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0px;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(hsl(211, 52%, 34%), transparent);
}
.header nav {
  position: relative;
  background-color: white;
  padding-top: 0.5625rem;
  padding-bottom: 0.5625rem;
}
.header__logo img {
  max-width: 50%;
}
.header__toggle > span {
  display: block;
  width: 26px;
  height: 3px;
  background-color: hsl(211, 52%, 34%);
  transition: all 300ms ease-in-out;
  transform-origin: 5px 1px;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 3px;
}
.header__toggle.open > span:first-child {
  transform: rotate(45deg);
}
.header__toggle.open > span:nth-child(2) {
  opacity: 0;
}
.header__toggle.open > span:last-child {
  transform: rotate(-45deg);
}
.header__menu {
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background: hsl(105, 55%, 96%);
  margin-top: 1.5rem;
  padding: 1.625rem;
  border-radius: 5px;
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: hsl(211, 52%, 34%);
  text-align: center;
}
.header__links a {
  position: relative;
  font-size: 1.125rem;
  color: hsl(211, 52%, 34%);
  transition: color 300ms ease-in-out;
}
.header__links a:not(:last-child) {
  margin-right: 32px;
}
.header__links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 5px;
  left: 0;
  right: 0;
  bottom: -30px;
  background: hsl(6, 63%, 53%);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links a:hover {
  color: hsl(6, 63%, 53%);
}
.header__links a:hover::before {
  opacity: 1;
}
.header__cta {
  transition: opacity 300ms ease-in-out;
}
.header__cta:hover {
  opacity: 0.75;
}

.hero {
  background-color: hsl(135, 8%, 72%);
  background-image: radial-gradient(circle at top left, rgba(34, 34, 34, 0.5) 0%, rgba(31, 62, 78, 0.6) 100%), url(/assets/images/kevin-rosales-3pz6HBp2Ye4-unsplash-resized.jpg);
  background-size: cover;
  padding-top: 11vw;
  padding-bottom: 11vw;
}
.hero__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__text {
  color: hsl(105, 55%, 96%);
  padding: 0 20%;
  text-align: center;
}
.hero__image {
  background-image: url(/assets/images/log-for-fav-transparent.png);
  background-size: cover;
  background-position: center center;
  padding: 5%;
}

.what_we_do_title {
  text-align: center;
  padding: 0 10%;
  margin-top: 5%;
  margin-bottom: 0;
  font-size: clamp(1.625rem, 2vw + 1rem, 4rem);
}

.what_we_do {
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(auto-fit, minmax(min(20rem, 100%), 1fr));
  text-align: center;
  padding: 5%;
  font-size: 1.125rem;
}

.we_do_images {
  display: block;
  max-width: 30%;
  margin: auto;
  padding: 2%;
}

.about_us {
  display: flex;
}

.about_us_images {
  display: inline;
  max-width: 10%;
  margin-bottom: auto;
  padding: 2%;
}

.checklist {
  width: 140rem;
  background-color: hsl(49, 6%, 34%);
  color: hsl(105, 55%, 96%);
}

.list_with_leftImage {
  display: flex;
}

.about {
  display: flex;
  flex-direction: column;
  background-color: hsl(105, 55%, 96%);
}

.about_us_title {
  margin-bottom: 0;
  text-align: center;
  font-size: clamp(1.625rem, 2vw + 1rem, 4rem);
}

.about_us_content {
  text-align: center;
}

.get_in_touch {
  background-color: hsl(6, 63%, 53%);
  color: hsl(105, 55%, 96%);
  padding: 1rem;
  gap: 10px;
}
.get_in_touch__heading {
  margin: 1px;
}
.get_in_touch__sentence {
  display: block;
  font-size: 0.875rem;
}

@media (max-width: 50em) {
  .about_us {
    flex-direction: column;
  }
  .checklist {
    width: 100%;
  }
  .get_in_touch__heading {
    margin: auto;
  }
  .get_in_touch__cta {
    margin: auto;
  }
}
.contact_us {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  background: white;
  width: 100%;
}
.contact_us__form {
  display: flex;
  flex-direction: column;
  background: hsl(182, 43%, 76%);
  padding: 2vw 4vw;
  width: 100%;
}
.contact_us__form h3 {
  color: hsl(211, 52%, 34%);
  font-weight: 700;
  margin-bottom: 20px;
}
.contact_us__form input, .contact_us form textarea {
  border: 0;
  margin: 10px 0;
  padding: 20px;
  outline: none;
  background: hsl(105, 55%, 96%);
  font-size: 16px;
}

.contact_box {
  max-width: 100%;
  height: auto;
}
.contact_box__social_icons {
  display: flex;
  justify-content: center;
  background-color: hsl(135, 8%, 72%);
}

.form_block {
  max-width: 100%;
}